@import "../../assets/scss/variables.scss";

.error-page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;

  &__type {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 75px;
    line-height: 88px;
    letter-spacing: -5.76923px;
    color: $gray-error;
    margin-bottom: 13px;
  }

  &__message {
    font-size: 50px;
    line-height: 57px;
    letter-spacing: -2.5px;
    color: $blue-gray;
    margin-bottom: 15px;
  }

  &__recommendation {
    font-size: 20px;
    line-height: 23px;
    letter-spacing: -0.5px;
    color: $gray-dark;
    margin-bottom: 35px;
  }
}
